@import './../../styles/index.scss';

.taskList {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  background-color: $c-bg-form;
  box-shadow: $box-shadow;

  @media screen and (max-width: 450px) {
    padding: 10px;
  }
}

.title {
  font-size: $fs-title;
  font-weight: $fw-bold;
  margin-bottom: 20px;
}

.addTaskContainer {
  display: flex;
  margin-bottom: 20px;
}

.addTask {
  flex: 1;
  padding: 13px;
  border: 2px solid;
  border-color: $c-border;
  border-radius: 5px 0px 0px 5px;
  font-size: $fs-add-btn;
  font-size: $fs-small;
  outline: none;

  &:focus {
    border-color: $c-add-btn;
  }

  @media screen and (max-width: 450px) {
    padding: 10px;
  }
}

.addTaskButton {
  background-color: $c-add-btn;
  color: $c-bg;
  border: 1px solid $c-add-btn;
  border-radius: 0px 5px 5px 0px;
  padding: 10px;
  cursor: pointer;
  transition: $t-normal;

  &:hover {
    background-color: $c-add-btn-hover;
  }
}

.noTasks {
  font-style: $f-decoration;
  color: $c-gray;
}
