// ? Fonts

// Font sizes
$fs-title: 26px;
$fs-add-btn: 18px;
$fs-small: 14px;
$fs-very-small: 12px;


// Font weights
$fw-regular: 400;
$fw-bold: 700;

// Font decoration
$f-decoration: italic;

// ? Line height
$line-height: 26px;


// ? Colors
$c-text: #000000;
$c-bg: #fff;
$c-gray: rgb(137, 136, 136);
$c-bg-form: #f2f2f2;
$c-border: #ddd;
$c-add-btn: #4caf50;
$c-add-btn-hover: #3e8e41;

// ? Box shadow
$box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);


// ? Transition
$t-normal: 0.35s cubic-bezier(0.4, 0, 0.2, 1);

// ? Media Queries: Breakpoints
$small: 320px;
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;
$screen: 1400px;
$large: 1920px;
